<template>
  <input :value="modelValue" @input="updateInput" class="input" type="text" />
</template>

<script>
export default {
  name: "my-input",
  props: {
    modelValue: [String, Number],
  },
  methods: {
    updateInput(event) {
      this.$emit("update:modelValue", event.target.value);
    },
  },
};
</script>

<style>
.input {
  color: #aeaeae;
  opacity: 1;
  width: 100%;
  border: 1px solid #243B67;
  padding: 10px 15px;
  margin-top: 15px;
}
</style>